@font-face {
  font-family: "Bitter";
  src: url(../font/Bitter-VariableFont_wght.ttf);
}
.authorization, .sign-in {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.authorization .creating-account,
.authorization .alreadyRegistered, .sign-in .creating-account,
.sign-in .alreadyRegistered {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.authorization .alreadyRegistered p, .sign-in .alreadyRegistered p {
  color: #0ebeac;
}

.movies-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 1;
}
.movies-container input {
  width: 50%;
}
.movies-container .movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 1vh 5.5vh;
}
.movies-container .movies li {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  margin: 0.5em;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  text-align: center;
}
.movies-container .movies li h2 {
  line-height: 1.3;
}
.movies-container .movies li img {
  width: 220px;
  height: 300px;
  transition: all 0.3s;
  opacity: 85%;
  margin-bottom: 1vh;
  border-radius: 22px;
}
.movies-container .movies li img:hover {
  transform: scale(1.05);
  opacity: 100%;
}
.movies-container .movies li h2 {
  width: 220px;
}

.movie {
  padding: 1em;
}
.movie .img-container {
  width: 220px;
  height: 330px;
  display: inline-block;
  vertical-align: top;
  border-radius: 22px;
  overflow: hidden;
  box-shadow: #6428cc 10px 10px 50px;
  transition: all 0.5s;
}
.movie .img-container img {
  display: block;
  max-width: 100%;
  min-height: 100%;
  border: none;
}
.movie .img-container:hover {
  box-shadow: #0ebeac 10px 10px 50px;
}
.movie h1 {
  font-size: xx-large;
  color: #6428cc;
}
.movie .movie-title h1 {
  margin: 0.5em 0;
}
.movie .movie-title .genres {
  margin: 0.5em 0;
}
.movie .movie-title .genres > * {
  background-color: rgba(100, 40, 204, 0.35);
  border: 1px solid rgba(100, 40, 204, 0.7);
  border-radius: 10px;
  padding: 0.25em 0.6em;
  margin-right: 0.5em;
  font-size: small;
  opacity: 90%;
}
.movie .additional-info {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1.5rem;
  height: -moz-fit-content;
  height: fit-content;
}
.movie .additional-info .play-btn {
  background-color: #6428cc;
  transition: all 0.5s;
}
.movie .additional-info .play-btn a img {
  filter: invert(100%);
}
.movie .additional-info .play-btn:hover {
  background-color: #0ebeac;
  border: 2px solid #0ebeac;
}
.movie .additional-info span {
  font-size: small;
  justify-content: flex-start;
  opacity: 50%;
  transform: rotate(-90deg);
  -webkit-hyphens: auto;
          hyphens: auto;
}
.movie .additional-info > * {
  display: flex;
  align-items: center;
  justify-content: center;
  justify-content: space-around;
  border: 2px solid rgba(100, 40, 204, 0.7);
  border-radius: 20px;
  margin-right: 1em;
  font-size: medium;
  height: 6rem;
  width: 10rem;
  padding: 1rem;
}
.movie .description {
  margin: 1em 0;
}
.movie .description h2 {
  opacity: 70%;
  margin-bottom: 0.5rem;
}
.movie .description p {
  font-size: small;
  opacity: 85%;
}

@media screen and (max-width: 650px) {
  .movie {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: auto;
  }
  .movie .img-container {
    width: 17em;
    height: 24em;
  }
  .movie .movie-info {
    text-align: center;
  }
  .movie .movie-info .additional-info {
    flex-wrap: wrap;
  }
  .movie .movie-info .additional-info > * {
    font-size: medium;
    margin: 1em;
    padding: 0.5rem;
  }
}
@media screen and (min-width: 650px) {
  .movie {
    display: flex;
    flex-wrap: wrap;
  }
  .movie .movie-info {
    text-align: justify;
    width: calc(100% - 220px - 3em);
    padding: 0 1em;
    margin-left: 1em;
  }
  .movie .movie-info .movie-title h1 {
    margin-bottom: 1rem;
  }
  .movie .movie-info .movie-title .genres {
    margin: 1.5em 0.5em 1em;
  }
  .movie .movie-info .additional-info {
    margin-top: 2.5rem;
  }
  .movie .movie-info .additional-info > * {
    margin-right: 0.5em;
    font-size: x-large;
  }
}
button {
  background-color: transparent;
  border: 2px solid #6428cc;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  margin: 1vh;
  transition: all 0.3s ease-in;
  font-family: "Bitter", Helvetica, serif;
}

button:hover {
  border: 2px solid #0ebeac;
  background-color: transparent;
  color: #0ebeac;
}

.scroll {
  width: 2rem;
  height: 2rem;
  position: fixed;
  bottom: 50px;
  right: 50px;
  background-color: #6428cc;
}

:disabled {
  color: #6428cc;
  cursor: copy;
}

:disabled:hover {
  background-color: transparent;
  border: 2px solid #6428cc;
  color: #6428cc;
  cursor: no-drop;
}

.buy-ticket {
  padding: 0.5em;
}

header {
  display: flex;
  flex-wrap: wrap;
  background-color: #000;
  justify-content: space-between;
  margin-bottom: 3.5vh;
  padding: 1em;
