  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 1rem 0.5rem;
  padding: 1rem;
  width: 100px;
  transition: all 0.5s;
}
.cinemas .show-date .place-and-time .place {
  font-size: small;
  margin-bottom: 0.5rem;
}
.cinemas .show-date .place-and-time .time {
  font-size: larger;
}
.cinemas .show-date .place-and-time:hover {
