  margin: 0 auto;
}
.user-data .form {
  background: #000;
  margin: 0 auto;
}
.user-data input {
  display: block;
  box-sizing: border-box;
  border-radius: 4px;
  border: 1px solid white;
  padding: 10px 15px;
  margin: 0.5em 0;
  font-size: 14px;
}
