}
.user-data button[type=submit],
.user-data input[type=submit] {
  display: block;
  background: #6428cc;
  color: white;
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  margin: 1em 0 0 0;
  padding: 1em;
  font-size: 16px;
  font-weight: 200;
  letter-spacing: 5px;
  transition: all 0.5s;
}
.user-data button[type=submit]:hover,
.user-data input[type=submit]:hover {
  background: #0ebeac;
}
.user-data button[type=submit]:active,
.user-data input[type=button]:active,
.user-data input[type=submit]:active {
  transition: 0.3s all;
  transform: translateY(3px);
  border: 1px solid transparent;
  opacity: 0.8;
}
.user-data input:disabled {
